/* General styling */

:root {
  --color-primary-100: #8153ff;
  --color-primary-300: #5928e5;
  --color-primary-500: #5124ce;
  --color-white-700: rgba(250, 250, 250, 0.8);

  --gradient-primary: linear-gradient(rgb(0, 148, 255), rgb(112, 0, 255));
  --gradient-primary-alt: linear-gradient(90deg, #0094ff 0%, #7000ff 100%);
}

body, html{
	margin: 0px;
	box-sizing: border-box;
	height: 100%;
	padding: 0px;
	font-family: 'Montserrat', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.page {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

a {
  color: #000;
  text-decoration: none;
}

.flex-box{
